.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.stopwatch {
  font-size: 24px;
  margin-bottom: 20px;
}

.quiz {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.question-panel {
  width: 25%;
  background-color: #f4f4f4;
  padding: 10px;
}

.question-panel ul {
  list-style: none;
  padding: 0;
}

.question-panel li {
  margin: 10px 0;
  cursor: pointer;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.question {
  width: 70%;
}

.question h2 {
  font-size: 24px;
}

.question ul {
  list-style: none;
  padding: 0;
}

.question li {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
}

.question li:hover {
  background-color: #e0e0e0;
}
