/* src/components/Navbar.css */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #282c34;
  color: white;
  padding: 10px 20px;
  width: 100%;
  box-sizing: border-box;
}

.navbar-brand h1 {
  margin: 0;
}

.navbar-links {
  display: flex;
  gap: 15px;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 5px;
}

.navbar-links a:hover {
  background-color: #61dafb;
  color: #282c34;
}

.navbar-stopwatch {
  display: flex;
  align-items: center;
}
