/* src/components/QuestionPanel.css */
.question-panel {
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-height: 500px;
    overflow-y: auto;
  }
  
  .question-panel h3 {
    margin: 0;
    font-size: 1.5rem;
    color: #333;
  }
  
  .question-panel ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .question-panel li {
    padding: 10px;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .question-panel li:hover {
    background-color: #e0e0e0;
  }
  
  .question-panel li.selected {
    background-color: #61dafb;
    color: #fff;
  }
  